import { observer, inject } from 'mobx-react';
import { compose, withProps } from 'recompose';
import { injectIntl } from 'react-intl';

const logic = compose(
  injectIntl,
  inject('mosaic', 'editableMosaic'),
  observer,
  withProps(
    ({
      editableMosaic: {
        isConfirmRemovePositions,
        closeConfirmRemovePositions,
        confirmRemovePositions,
        isConfirmRemoveLayer,
        closeConfirmRemoveLayer,
        confirmRemoveLayer
      },
      mosaic: { isConfirmSelectMosaic, closeConfirmSelectMosaic, confirmSelectMosaic }
    }) => {
      if (isConfirmRemovePositions) {
        return {
          isConfirm: isConfirmRemovePositions,
          close: closeConfirmRemovePositions,
          confirm: confirmRemovePositions
        };
      }
      if (isConfirmRemoveLayer) {
        return {
          isConfirm: isConfirmRemoveLayer,
          close: closeConfirmRemoveLayer,
          confirm: confirmRemoveLayer
        };
      }
      if (isConfirmSelectMosaic) {
        return {
          isConfirm: isConfirmSelectMosaic,
          close: closeConfirmSelectMosaic,
          confirm: confirmSelectMosaic
        };
      }
      return {
        isConfirm: false
      };
    }
  )
);

export default logic;
