export default function checkCollision(
  layout,
  dimension,
  itemMargin,
  rowHeight,
  width,
  allowHoveredPosition,
  event
) {
  const mouseX = event.clientX;
  const mouseY = event.clientY;
  const { left, top } = event.currentTarget.getBoundingClientRect();
  const layoutLeft = left + itemMargin;
  const layoutTop = top + itemMargin;
  const rowWidth = Math.floor((width - 2 * itemMargin) / dimension);
  const xPosition = Math.floor((mouseX - layoutLeft) / rowWidth);
  const yPosition = Math.floor((mouseY - layoutTop) / rowWidth);
  const collision = layout.find(
    item =>
      !item.static &&
      xPosition >= item.x &&
      xPosition < item.x + item.w &&
      yPosition >= item.y &&
      yPosition < item.y + item.h
  );

  if (collision) {
    return { visible: false };
  }
  if (
    !collision &&
    allowHoveredPosition &&
    xPosition >= 0 &&
    yPosition >= 0 &&
    xPosition < dimension &&
    yPosition < dimension
  ) {
    const hoverX = itemMargin + xPosition * itemMargin + xPosition * rowHeight;
    const hoverY = itemMargin + yPosition * itemMargin + yPosition * rowHeight;

    return {
      x: xPosition,
      y: yPosition,
      hoverX,
      hoverY,
      hoverWidth: rowHeight,
      visible: true
    };
  }
  return { visible: false };
}
