import React from 'react';
import { compose, onlyUpdateForKeys } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';

import styles from './styles';

const materialStyles = {
  expandButton: {
    width: '2vw',
    height: '2vw',
    padding: 0
  }
};

export const IconShowMore = ({ classes, prstsVsblChangeHndlr, isPrstsVisble }) => (
  <IconButton
    className={classes.expandButton}
    aria-label="ExpandMore"
    onClick={prstsVsblChangeHndlr}
  >
    {isPrstsVisble ? <ExpandMore style={styles.icon} /> : <ExpandLess style={styles.icon} />}
  </IconButton>
);
const EnhancedIconShowMore = compose(
  onlyUpdateForKeys(['isPrstsVisble']),
  withStyles(materialStyles)
)(IconShowMore);

export default EnhancedIconShowMore;
