const getPosibleGrids = (sizes, cols, blockSize) => {
  const result = sizes
    .map(size => ({ grid: size, blockSize: size / (cols / blockSize) }))
    .filter(({ grid, blockSize }) => blockSize % 1 === 0)
    .map(({ grid, blockSize }) => grid);

  return result.length > 0 ? result : [];
};

const calcIndents = (sizes, arr, cols) => {
  if (arr.length) {
    const result = [];

    arr.forEach(item => {
      const positionIndents = {
        w: item.w,
        xLeft: item.x,
        xRght: cols - (item.x + item.w),
        h: item.h,
        yTop: item.y,
        yBttm: cols - (item.y + item.h)
      };

      Object.values(positionIndents).forEach(indentValue =>
        result.push(getPosibleGrids(sizes, cols, indentValue))
      );
    });
    const arrays = result.filter(({ length }) => length > 0);

    return sizes.filter(size =>
      arrays.reduce((prev, curr) => prev && curr.includes(size), arrays[0].includes(size))
    );
  }
  return sizes;
};

export default calcIndents;
