import { observer, inject } from 'mobx-react';
import { compose, withProps } from 'recompose';
import { injectIntl } from 'react-intl';

const logic = compose(
  injectIntl,
  inject('mosaic'),
  observer,
  withProps(
    ({ mosaic: { isConfirmRemoveMosaic, closeConfirmRemoveMosaic, confirmRemoveMosaic } }) => ({
      isConfirm: isConfirmRemoveMosaic,
      close: closeConfirmRemoveMosaic,
      confirm: confirmRemoveMosaic
    })
  )
);

export default logic;
