export default function createConfig(layers) {
  const layersLength = layers.length;
  const cfgDimension = [];
  const cfgPositions = [];
  const setPositions = [];
  const condition = role => role === 1 || role === 2 || role === 3 || role === 4;

  layers.forEach(({ positions, dimension }, index) => {
    const layerIndex = index;

    cfgDimension.push(dimension);
    positions.forEach(({ i, x, y, w, role }) => {
      cfgPositions.push(`${layerIndex},${x},${y},${w}`);
      if (condition(role)) setPositions.push(`${layerIndex},${Number(i) - 1},${role},0,0`);
    });
  });
  const dimension = cfgDimension.join('\n');
  const positions = cfgPositions.join('\n');
  const cfg = `#${layersLength}\n${dimension}\n${positions}`;
  const set = setPositions.join('\n');

  return { cfg, set };
}
