import React from 'react';
import { observer } from 'mobx-react';
import RGL from 'react-grid-layout';
import 'react-grid-layout/css/styles.css';
import PresetPosition from 'components/PresetPosition';

const rowHeightCalc = (presetWidth, itemMargin, dimension) =>
  (presetWidth - 1 * itemMargin) / dimension - itemMargin;

const PresetPositionLayout = ({ positions, dimension, itemMargin, presetWidth }) => (
  <RGL
    margin={[itemMargin, itemMargin]}
    compactType={null}
    autoSize={false}
    preventCollision
    {...{
      isDraggable: false,
      isResizable: false,
      cols: dimension,
      maxRows: dimension,
      rowHeight: rowHeightCalc(presetWidth, itemMargin, dimension),
      layout: positions,
      width: presetWidth
    }}
  >
    {positions.map(({ i, role }, index) => (
      <PresetPosition {...{ key: i, i, role }} />
    ))}
  </RGL>
);
const EnhancedPresetPositionLayout = observer(PresetPositionLayout);

export default EnhancedPresetPositionLayout;
