export default function getMaxResizeBYCollide(layout, item) {
  const collides = [];

  layout.filter(({ i }) => i !== item.i).forEach(({ x, y, w, h }) => {
    if ((item.y > y && y + h > item.y) || (item.y <= y && item.y + item.h >= y)) {
      collides.push(x - (item.x + item.w));
    }
    if ((item.x > x && x + w > item.x) || (item.x <= x && item.x + item.w >= x)) {
      collides.push(y - (item.y + item.h));
    }
    if (x - (item.x + item.w) === y - (item.y + item.h)) {
      collides.push(y - (item.y + item.h));
    }
  });
  const minByCollide = Math.min(...collides.filter(item => item >= 0));

  return {
    maxW: item.w + minByCollide,
    maxH: item.h + minByCollide
  };
}
