import { observer, inject } from 'mobx-react';
import { compose, withProps } from 'recompose';
import { injectIntl } from 'react-intl';

const logic = compose(
  injectIntl,
  inject('actionMessage'),
  observer,
  withProps(
    ({
      actionMessage: { isActionMessage, closeActionMessage, actionMessageState, actionMessageText }
    }) => ({
      isActionMessage,
      closeActionMessage,
      actionMessageState,
      actionMessageText
    })
  )
);

export default logic;
