import { reaction } from 'mobx';

const reactions = ({ mosaic, actionMessage }) => {
  reaction(
    () => actionMessage.isActionMessage,
    isActionMessage => {
      if (!isActionMessage) {
        mosaic.isLoadedOff();
        mosaic.isLoadErrorOff();
        mosaic.isSavedOff();
        mosaic.isSaveErrorOff();
      }
    }
  );
};

export default reactions;
