import sizeMe from 'react-sizeme';
import { observer, inject } from 'mobx-react';
import { compose, withProps, withHandlers } from 'recompose';
import isEqual from 'lodash.isequal';
import { values } from 'mobx';

function checkLayerChanged(layers, selectedMosaic, selectedLayer, getPresetById) {
  const mosaic = getPresetById(selectedMosaic.toString());

  if (mosaic) {
    const savedLayer = mosaic.layers[selectedLayer];
    const seleedtdLayer = layers[selectedLayer];

    if (savedLayer) {
      if (selectedMosaic) {
        const { visible, ...selecteToCompare } = seleedtdLayer;
        const { visible: vsble, ...savedToCompare } = savedLayer;

        return isEqual(selecteToCompare, savedToCompare);
      }
      return seleedtdLayer.positions.length === 0 && seleedtdLayer.dimension === 3;
    }
  }
  return false;
}
const logic = compose(
  sizeMe(),
  inject('mosaic', 'editableMosaic', 'positionModal'),
  observer,
  withHandlers({
    removeLayer: ({
      editableMosaic: {
        mosaic: { layers, selectedLayer },
        removeLayer,
        openConfirmRemoveLayer
      },
      mosaic: { selectedMosaic, getPresetById }
    }) => index => {
      if (checkLayerChanged(layers, selectedMosaic, index, getPresetById)) {
        removeLayer(index);
      } else {
        openConfirmRemoveLayer(index);
      }
    },
    addLayerHandler: ({
      editableMosaic: {
        mosaic: { addLayer }
      }
    }) => () => {
      addLayer();
    }
  }),
  observer,
  withProps(
    ({
      positionModal: { positionModalShow, holdTimerCount },
      size: { width },
      editableMosaic: {
        mosaic: { id, layers, remove, selectedLayer, selectLayer },
        layersMoreThanOne,
        layersLessThanThree,
        getLowestVisible
      }
    }) => ({
      id,
      layers,
      valuesLayers: values(layers),
      remove,
      selectedLayer,
      selectLayer,
      width,
      positionModalShow,
      holdTimerCount,
      layersMoreThanOne,
      layersLessThanThree,
      getLowestVisible
    })
  ),
  observer
);

export default logic;
