import React from 'react';
import { compose, withHandlers, onlyUpdateForKeys } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

const materialStyles = {
  success: {
    color: '#1565C0',
    margin: '0.5vw',
    alignSelf: 'center',
    fontSize: '1vw',
    padding: '0.5vw'
  },
  danger: {
    color: '#EF5350',
    margin: '1vw',
    alignSelf: 'center',
    fontSize: '1vw',
    padding: '0.5vw'
  }
};
const ActionButton = ({ classes, text, onClickHandler, type }) => (
  <Button className={classes[type]} onClick={onClickHandler}>
    {text}
  </Button>
);
const EnhancedActionButton = compose(
  withHandlers({
    onClickHandler: ({ onClick }) => () => {
      onClick();
    }
  }),
  withStyles(materialStyles),
  onlyUpdateForKeys([])
)(ActionButton);

export default EnhancedActionButton;
