const getOpacity = index => 1 - 0.1 * index;

const rolesColor = index => {
  const opacity = getOpacity(index);

  return [
    `rgba(224, 224, 224, ${opacity})`,
    `rgba(128, 203, 196, ${opacity})`,
    `rgba(255, 183, 77, ${opacity})`,
    `rgba(189, 189, 189, ${opacity})`,
    `rgba(179, 229, 252, ${opacity})`
  ];
};

export default function createPng(layers) {
  const div = document.createElement('div');

  div.id = 'canvasWrapper';
  document.body.appendChild(div);
  document.getElementById('canvasWrapper').innerHTML =
    '<canvas id="myCanvas" width="640" height="360"></canvas>';
  let canvas = document.getElementById('myCanvas');
  let context = canvas.getContext('2d');

  layers.forEach(({ positions, dimension }, index) => {
    positions.forEach(({ i, x, y, w, h, role }) => {
      paintCnvs(i, x, y, w, h, role, dimension, context, index);
    });
  });
  let cnvs = document.getElementById('myCanvas');
  let img = cnvs.toDataURL('image/png');

  document.getElementById('canvasWrapper').innerHTML = '';
  document.body.removeChild(div);
  return img;
}

function paintCnvs(i, x, y, w, h, role, collCount, context, index) {
  let margin = 2.5;
  let collWidth = 640 / collCount;
  let collHeight = 360 / collCount;
  let rectX = x * collWidth + margin;
  let rectY = y * collHeight + margin;
  let rectHeight = h * collHeight - 2 * margin;
  let rectWidth = w * collWidth - 2 * margin;
  let cornerRadius = 5;
  let fontSize = rectHeight / 2;

  context.lineJoin = 'round';
  context.lineWidth = cornerRadius;

  context.strokeStyle = '#424242';

  context.strokeRect(
    rectX + cornerRadius / 2,
    rectY + cornerRadius / 2,
    rectWidth - cornerRadius,
    rectHeight - cornerRadius
  );
  context.fillStyle = rolesColor(index)[role];
  context.fillRect(
    rectX + cornerRadius / 2,
    rectY + cornerRadius / 2,
    rectWidth - cornerRadius,
    rectHeight - cornerRadius
  );
  context.fillStyle = '#424242';
  context.font = `lighter ${fontSize}px Arial`;
  context.textAlign = 'center';
  context.textBaseline = 'middle';
  context.fillText(
    i,
    rectX + cornerRadius / 2 + (rectWidth - cornerRadius) / 2,
    rectY + cornerRadius / 2 + (rectHeight - cornerRadius) / 2
  );
}
