const styles = {
  wrapper: (width, isLowestVisible) => ({
    width,
    height: width,
    backgroundColor: isLowestVisible ? 'rgba(185, 215, 239, 0.5)' : 'transparent',
    position: 'relative'
  }),
  hoverStyle: ({ topX, topY, width }) => ({
    cursor: 'pointer',
    position: 'absolute',
    left: topX,
    top: topY,
    width,
    height: width,
    backgroundColor: '#aaaaaacc',
    textAlign: 'center',
    WebkitTransition: 'all 200ms ease'
  })
};

export default styles;
