import React from 'react';
import CustomPositionButton from 'components/CustomPositionButton';
import DeleteIcon from 'components/DeleteIcon';
import styles from './styles';
import logic from './logic';

const CustomPosition = ({
  i,
  role,
  roleColor,
  removePositionHandler,
  style,
  children,
  selectPositionHandler,
  positionModalShow,
  isSmall,
  isMedium,
  isLarge,
  rowHeight,
  isSelectedLayer,
  ...props
}) => {
  const positionStyle = { ...style, ...styles.block(roleColor, isSelectedLayer) };

  return (
    <div {...props} style={positionStyle}>
      {!isSmall && isSelectedLayer && <DeleteIcon onClick={removePositionHandler} />}
      <CustomPositionButton
        {...{
          isSelectedLayer,
          modalOpen: selectPositionHandler,
          isSmall,
          isMedium,
          isLarge,
          role,
          i
        }}
      />
      {isSelectedLayer && children}
    </div>
  );
};

const EnhancedCustomPosition = logic(CustomPosition);

export default EnhancedCustomPosition;
