import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import messagesId from 'translation/messagesId';
import Snackbar from '@material-ui/core/Snackbar';
import styles from './styles';

const anchorOrigin = {
  vertical: 'bottom',
  horizontal: 'center'
};

const ActionMessage = ({
  intl,
  classes,
  isActionMessage,
  closeActionMessage,
  actionMessageState,
  actionMessageText
}) => (
  <Snackbar
    className={classes.root}
    anchorOrigin={anchorOrigin}
    open={isActionMessage}
    onClose={closeActionMessage}
    ContentProps={{
      className: actionMessageState ? classes.success : classes.error
    }}
    message={actionMessageText && intl.formatMessage(messagesId[actionMessageText])}
  />
);

const EnhancedActionMessage = withStyles(styles)(ActionMessage);

export default EnhancedActionMessage;
