import { types as typesMST } from 'mobx-state-tree';

const types = {
  i: typesMST.string,
  x: typesMST.number,
  y: typesMST.number,
  w: typesMST.number,
  h: typesMST.number,
  role: typesMST.optional(typesMST.number, 0),
  maxW: typesMST.maybe(typesMST.number, null),
  maxH: typesMST.maybe(typesMST.number, null)
};

export default types;
