import { reaction } from 'mobx';

const reactions = ({ mosaic, presets, defaultPresetsPage, customPresetsPage }) => {
  reaction(
    () => presets.customVisible,
    customVisible => {
      const presetsOnPage = presets.presetsOnPage;
      const page = defaultPresetsPage.page;
      const presetsLength = mosaic.getDefaultPresetsLength;
      const newPage = Math.floor(presetsLength / presetsOnPage);

      if (!customVisible) {
        if (page > 0) {
          if (presetsOnPage >= presetsLength % presetsOnPage) {
            defaultPresetsPage.setPage(newPage);
          }
          if (presetsLength % presetsOnPage === 0) {
            defaultPresetsPage.pagePrev();
          }
        }
      }
    }
  );
  reaction(
    () => presets.defaultVisible,
    defaultVisible => {
      const presetsOnPage = presets.presetsOnPage;
      const page = customPresetsPage.page;
      const presetsLength = mosaic.getCustomPresetsLength;
      const newPage = Math.floor(presetsLength / presetsOnPage);

      if (!defaultVisible) {
        if (page > 0) {
          if (presetsOnPage >= presetsLength % presetsOnPage) {
            customPresetsPage.setPage(newPage);
          }
          if (presetsLength % presetsOnPage === 0) {
            customPresetsPage.pagePrev();
          }
        }
      }
    }
  );
};

export default reactions;
