import React from 'react';

import Tab from '@material-ui/core/Tab';
import { withStyles } from '@material-ui/core/styles';
import messagesId from 'translation/messagesId';
import DeleteIcon from 'components/DeleteIcon';
import ShowLayer from 'components/ShowLayer';

import logic from './logic';
import styles from './styles';

export const LayerItem = ({
  intl,
  classes,
  index,
  lowestThanSelected,
  layersMoreThanOne,
  removeLayer,
  removeLayerHandler,
  visible,
  visibleOffHandler,
  visibleOnHandler,
  selectedLayer,
  ...props
}) => (
  <div className={classes.tabWrapper}>
    {layersMoreThanOne && <DeleteIcon onClick={removeLayerHandler} />}
    {lowestThanSelected && <ShowLayer {...{ visible, visibleOffHandler, visibleOnHandler }} />}
    <Tab
      label={intl.formatMessage(messagesId.layer, { index })}
      {...props}
      classes={{
        root: classes.tabRoot,
        selected: classes.tabSelected,
        labelContainer: classes.labelContainer
      }}
    />
  </div>
);
const EnhancedLayerItem = logic(withStyles(styles)(LayerItem));

export default EnhancedLayerItem;
