import React from 'react';
import { compose, onlyUpdateForKeys } from 'recompose';
import { injectIntl } from 'react-intl';
import { withStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import NavigateNext from '@material-ui/icons/NavigateNext';
import NavigateBefore from '@material-ui/icons/NavigateBefore';
import messagesId from 'translation/messagesId';
import styles from './styles';

const materialStyles = {
  button: {
    margin: '0.2vw',
    width: '2vw',
    height: '2vw',
    padding: 0
  }
};

const PresetPagination = ({
  classes,
  count,
  rowsPerPage,
  page,
  pagePrevHandler,
  pageNextHandler,
  intl,
  isLast = page >= count / rowsPerPage - 1,
  firstOnPage = page * rowsPerPage + 1,
  lastOnPage = isLast ? count : page * rowsPerPage + rowsPerPage
}) => (
  <div style={styles.paginationWrapper}>
    <span style={styles.paginationText}>
      {intl.formatMessage(messagesId.paginationText, { firstOnPage, lastOnPage, count })}
    </span>
    <IconButton
      className={classes.button}
      onClick={pagePrevHandler}
      aria-label="Prev Page"
      disabled={page === 0}
    >
      <NavigateBefore style={styles.icon} />
    </IconButton>
    <IconButton
      className={classes.button}
      onClick={pageNextHandler}
      aria-label="Next Page"
      disabled={isLast}
    >
      <NavigateNext style={styles.icon} />
    </IconButton>
  </div>
);

const EnhancedPresetPagination = compose(
  injectIntl,
  onlyUpdateForKeys(['count', 'rowsPerPage', 'page']),
  withStyles(materialStyles)
)(PresetPagination);

export default EnhancedPresetPagination;
