import { values } from 'mobx';

const views = self => ({
  get getCustomPresetsLength() {
    return values(self.customPresets).length;
  },
  get getDefaultPresetsLength() {
    return values(self.defaultPresets).length;
  },
  getPresetById(selectedMosaic) {
    const presetsList = self.isDefaultSelected ? self.defaultPresets : self.customPresets;

    return presetsList.find(({ id }) => id === selectedMosaic);
  }
});

export default views;
