import { types as typesMST } from 'mobx-state-tree';
import Mosaic from '../Mosaic';

const types = {
  idToselectMosaic: typesMST.maybe(typesMST.string),
  isDefaultToselectMosaic: typesMST.maybe(typesMST.boolean),
  isConfirmSelectMosaic: typesMST.optional(typesMST.boolean, false),
  isConfirmRemoveMosaic: typesMST.optional(typesMST.boolean, false),
  isLoaded: typesMST.optional(typesMST.boolean, false),
  isLoadError: typesMST.optional(typesMST.boolean, false),
  isSaved: typesMST.optional(typesMST.boolean, false),
  isSaveError: typesMST.optional(typesMST.boolean, false),
  isDelete: typesMST.optional(typesMST.boolean, false),
  isDeleteError: typesMST.optional(typesMST.boolean, false),
  idToRemoveMosaic: typesMST.maybe(typesMST.string),
  isDefaultSelected: typesMST.optional(typesMST.boolean, true),
  selectedMosaic: typesMST.number,
  defaultPresets: typesMST.array(Mosaic),
  customPresets: typesMST.array(Mosaic)
};

export default types;
