import React from 'react';
import { observer } from 'mobx-react';
import PresetMosaic from 'components/PresetMosaic';
import PresetPagination from 'components/PresetPagination';
import IconShowMore from 'components/IconShowMore';
import styles from './styles';
import logic from './logic';

const PresetColumn = ({
  text,
  isDefault,
  selectedMosaic,
  presetsList,
  confirmSelectMosaic,
  isVisible,
  presetsOnPage,
  changeVisibleHandler,
  presetMargin,
  prstCntnrWidth,
  itemMargin,
  presetWidth,
  pagePrevHandler,
  pageNextHandler,
  presetTodisplay,
  presetslistLength,
  page,
  isPagination
}) => (
  <div style={styles.typePresetWrapper(isVisible)}>
    <div style={styles.mozaicTitleWrapper} onClick={changeVisibleHandler}>
      <h3 style={styles.textTitle}>
        {text} {presetslistLength}
      </h3>
      <IconShowMore {...{ isPrstsVisble: isVisible }} />
    </div>
    {isVisible && (
      <div style={styles.presetTopWrapper}>
        <div style={styles.presetWrapper}>
          {presetTodisplay.map(mosaic => (
            <PresetMosaic
              {...{
                key: mosaic.id,
                isDefault,
                mosaic,
                confirmSelectMosaic,
                selectedMosaic,
                presetMargin,
                prstCntnrWidth,
                itemMargin,
                presetWidth
              }}
            />
          ))}
        </div>
        {isPagination && (
          <PresetPagination
            {...{
              count: presetslistLength,
              rowsPerPage: presetsOnPage,
              page,
              pagePrevHandler,
              pageNextHandler
            }}
          />
        )}
      </div>
    )}
  </div>
);
const EnhancedPresetColumn = logic(observer(PresetColumn));

export default EnhancedPresetColumn;
