import React from 'react';
import GridLayout from 'components/GridLayout';
import HoverPosition from 'components/HoverPosition';
import Grid from 'components/Grid';

import styles from './styles';
import logic from './logic';

const EditMosaicLayout = ({
  positions,
  dimension,
  itemMargin,
  rowHeight,
  width,
  changeCoordinates,
  changeWidth,
  changeMaxWidth,
  addPosition,
  addHoverPosition,
  clearHoverPosition,
  addPositionHandler,
  hoverPosition,
  allowHoveredPosition,
  allowHoveredPositionChange,
  positionModalShow,
  holdTimerCount,
  hoverX,
  hoverY,
  hoverWidth,
  visible,
  isSelectedLayer,
  isLowestVisible
}) => (
  <div
    style={styles.wrapper(width, isLowestVisible)}
    onMouseMove={addHoverPosition}
    onMouseLeave={clearHoverPosition}
  >
    {isSelectedLayer && (
      <Grid
        {...{
          width,
          rowHeight,
          itemMargin
        }}
      />
    )}
    <GridLayout
      {...{
        positions,
        dimension,
        itemMargin,
        rowHeight,
        width,
        changeCoordinates,
        changeWidth,
        changeMaxWidth,
        allowHoveredPositionChange,
        positionModalShow,
        holdTimerCount,
        isSelectedLayer
      }}
    />
    {visible && <HoverPosition {...{ addPositionHandler, hoverX, hoverY, hoverWidth }} />}
  </div>
);
const EnhancedEditMosaicLayout = logic(EditMosaicLayout);

export default EnhancedEditMosaicLayout;
