import { observer } from 'mobx-react';
import { injectIntl } from 'react-intl';
import React from 'react';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import messagesId from 'translation/messagesId';

import styles from './styles';

const CustomPositionButton = ({
  intl,
  isSelectedLayer,
  modalOpen,
  isSmall,
  isMedium,
  isLarge,
  role,
  i
}) => (
  <Button style={styles.editButton} onClick={modalOpen}>
    <div style={styles.key(isSelectedLayer)}>
      <Typography variant="title" style={styles.positionText(isMedium, isLarge)}>
        {i}
      </Typography>
      {!isSmall && (
        <Typography variant="title" style={styles.roleText(isLarge)}>
          {intl.formatMessage(messagesId[role])}
        </Typography>
      )}
    </div>
  </Button>
);
const EnhancedCustomPositionButton = observer(injectIntl(CustomPositionButton));

export default EnhancedCustomPositionButton;
