import React from 'react';
import { values } from 'mobx';
import { observer } from 'mobx-react';
import messagesId from 'translation/messagesId';
import PresetColumn from 'components/PresetColumn';
import styles from './styles';

const PresetsBlock = ({
  intl,
  selectedMosaic,
  defaultPresets,
  customPresets,
  confirmSelectMosaic,
  defaultVisible,
  customVisible,
  presetsOnPage,
  changeDefaultVisibleHandler,
  changeCustomVisibleHandler,
  selectedDefaultMosaic,
  selectedCustomMosaic,
  presetMargin,
  prstCntnrWidth,
  itemMargin,
  presetWidth,
  defaultPresetsPage,
  customPresetsPage
}) => (
  <div style={styles.wrapper}>
    <PresetColumn
      {...{
        text: intl.formatMessage(messagesId.layoutTemplates),
        isDefault: true,
        selectedMosaic: selectedDefaultMosaic,
        presetsList: values(defaultPresets),
        confirmSelectMosaic,
        isVisible: defaultVisible,
        presetsOnPage,
        changeVisibleHandler: changeDefaultVisibleHandler,
        presetMargin,
        prstCntnrWidth,
        itemMargin,
        presetWidth,
        page: defaultPresetsPage
      }}
    />
    <PresetColumn
      {...{
        text: intl.formatMessage(messagesId.createdTemplates),
        isDefault: false,
        selectedMosaic: selectedCustomMosaic,
        presetsList: values(customPresets),
        confirmSelectMosaic,
        isVisible: customVisible,
        presetsOnPage,
        changeVisibleHandler: changeCustomVisibleHandler,
        presetMargin,
        prstCntnrWidth,
        itemMargin,
        presetWidth,
        page: customPresetsPage
      }}
    />
  </div>
);
const EnhancedPresetsBlock = observer(PresetsBlock);

export default EnhancedPresetsBlock;
