function positionTextFontSize(isMedium, isLarge) {
  if (isLarge) {
    return '15%';
  }
  if (isMedium) {
    return '20%';
  }
  return '50%';
}
const styles = {
  key: isSelectedLayer => ({
    margin: 0,
    padding: 0,
    fontSize: 'inherit',
    flexGrow: 1,
    flexShrink: 0,
    flexBasis: 'auto',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignContent: 'center',
    alignItems: 'center',
    textShadow: isSelectedLayer ? '0 0 30px #878788' : '0 0 50px #878788'
  }),
  positionText: (isMedium, isLarge) => ({
    fontSize: positionTextFontSize(isMedium, isLarge),
    margin: isLarge ? '10px' : '2.5px',
    color: '#ffffff'
  }),
  roleText: isLarge => ({
    padding: 0,
    maxWidth: '90%',
    flexGrow: 1,
    flexShrink: 0,
    flexBasis: 'auto',
    margin: isLarge ? '10px' : '2.5px',
    fontSize: isLarge ? '10%' : '12%',
    color: '#ffffff',
    wordwrap: 'normal'
  }),
  editButton: {
    margin: 0,
    padding: 0,
    minWidth: 0,
    maxWidth: '100%',
    height: '100%',
    textTransform: 'none',
    fontSize: 'inherit',
    flexGrow: 1,
    flexShrink: 0,
    flexBasis: 'auto',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: '2px'
  }
};

export default styles;
