import { observer } from 'mobx-react';
import { compose, withProps, withHandlers } from 'recompose';

const checkIsSelected = (id, selectedMosaic) => Number(id) === selectedMosaic;

const defaultLayer = 0;

const logic = compose(
  observer,
  withHandlers({
    removeHandler: ({ mosaic: { openConfirmRemoveMosaic } }) => () => {
      openConfirmRemoveMosaic();
    },
    selectMosaicHandler: ({ mosaic: { id }, confirmSelectMosaic, isDefault }) => () => {
      confirmSelectMosaic(id, isDefault);
    }
  }),
  withProps(
    ({ mosaic: { id, layers }, selectedMosaic, itemMargin, presetWidth, removeHandler }) => {
      const { positions, dimension } = layers[defaultLayer];

      return {
        isSelected: checkIsSelected(id, selectedMosaic),
        positions,
        dimension,
        layers
      };
    }
  )
);

export default logic;
