import { getParent, destroy } from 'mobx-state-tree';

const initialPosition = { w: 1, h: 1, role: 0 };

function getChangePosition(positions, i) {
  const item = positions.find(({ i: itemI }, index) => i === itemI);

  return { item, index: positions.indexOf(item) };
}
const actions = self => {
  function addPosition(x, y) {
    self.positions.push({
      ...initialPosition,
      x,
      y,
      i: getParent(self, 2).getNewtPositionNum()
    });
  }
  function changeCoordinates(i, x, y) {
    const { item, index } = getChangePosition(self.positions, i);
    const { w, h, role } = item;

    self.positions.set(index, { i, w, h, role, x, y });
  }
  function changeWidth(i, w, h) {
    const { item, index } = getChangePosition(self.positions, i);
    const { x, y, role } = item;

    self.positions.set(index, { i, w, h, role, x, y });
  }
  function changeMaxWidth({ i, maxW, maxH }) {
    const { item, index } = getChangePosition(self.positions, i);
    const { x, y, w, h, role } = item;

    self.positions.set(index, { i, w, h, role, x, y, maxW, maxH });
  }
  function remove() {
    getParent(self, 2).removeLayer(self);
  }
  function removePosition(item) {
    destroy(item);
    getParent(self, 2).remappingPositionsNum();
  }
  function changeDimension(dimension) {
    getParent(self, 2).changeLayerDimension(dimension);
  }
  function visibleOff() {
    self.visible = false;
  }
  function visibleOn() {
    self.visible = true;
  }
  return {
    addPosition,
    removePosition,
    remove,
    changeDimension,
    changeCoordinates,
    changeWidth,
    changeMaxWidth,
    visibleOff,
    visibleOn
  };
};

export default actions;
