import { observer } from 'mobx-react';
import { compose, withHandlers } from 'recompose';
import { injectIntl } from 'react-intl';

export const logic = withHandlers({
  selectLayerHandler: ({ selectLayer }) => (event, index) => {
    selectLayer(index);
  }
});
const composedLogic = compose(
  injectIntl,
  logic,
  observer
);

export default composedLogic;
