import { compose, withHandlers, withProps } from 'recompose';
import { observer } from 'mobx-react';
import { roles, rolesColors } from 'constants/roles';

const logic = compose(
  observer,
  withProps(({ position: { w, role, i }, rowHeight, thisHeight = rowHeight * w }) => ({
    i,
    isSmall: thisHeight <= 50,
    isMedium: thisHeight > 50 && thisHeight < 200,
    isLarge: thisHeight >= 200,
    role: roles[role],
    roleColor: rolesColors()[role]
  })),
  withHandlers({
    removePositionHandler: ({ position: { remove } }) => () => {
      remove();
    },
    selectPositionHandler: ({ position: { selectPosition }, positionModalShow }) => event => {
      selectPosition();
      positionModalShow(event.currentTarget);
    }
  })
);

export default logic;
