import React from 'react';
import { pure } from 'recompose';
import styles from './styles';

export const Grid = ({
  width,
  rowHeight,
  itemMargin,
  paletteWith = width - itemMargin,
  sttrokeWidth = width / 200,
  viewBox = paletteWith + sttrokeWidth / 2,
  size = rowHeight + itemMargin
}) => (
  <svg
    style={styles.canvas(paletteWith)}
    viewBox={`0 0 ${viewBox} ${viewBox}`}
    xmlns="http://www.w3.org/2000/svg"
  >
    <defs>
      <pattern id="grid" width={size} height={size} patternUnits="userSpaceOnUse">
        <path
          d={`M ${size} 0 L 0 0 0 ${size}`}
          fill="none"
          stroke="#f5005744"
          strokeWidth={sttrokeWidth}
          strokeOpacity="1"
        />
      </pattern>
    </defs>
    <rect data-type="grid" width="100%" height="100%" fill="url(#grid)" />
  </svg>
);
const EnhancedGrid = pure(Grid);

export default EnhancedGrid;
