import { observer } from 'mobx-react';
import { values } from 'mobx';
import { compose, withProps, branch, renderNothing } from 'recompose';

const logic = compose(
  observer,
  withProps(
    ({
      layer: {
        dimension,
        positions,
        addPosition,
        changeCoordinates,
        changeWidth,
        changeMaxWidth,
        visible
      },
      index,
      width,
      positionModalShow,
      holdTimerCount,
      selectedLayer,
      getLowestVisible
    }) => {
      const positionsValues = values(positions);
      const isSelectedLayer = index === selectedLayer;
      const isLowestThanSelected = index <= selectedLayer;
      const isInvisible = !visible && !isSelectedLayer;
      const isLowestVisible = getLowestVisible === index;

      return {
        positions: positionsValues,
        dimension,
        isSelectedLayer,
        width,
        changeCoordinates,
        changeWidth,
        changeMaxWidth,
        addPosition,
        positionModalShow,
        holdTimerCount,
        isLowestThanSelected,
        isInvisible,
        isLowestVisible
      };
    }
  ),
  branch(
    ({ isLowestThanSelected, isInvisible }) => !isLowestThanSelected || isInvisible,
    renderNothing
  )
);

export default logic;
