import React from 'react';

import MosaicLayoutWrapper from 'components/MosaicLayoutWrapper';
import LayersBlock from 'components/LayersBlock';

import styles from './styles';

const EditBlock = ({
  id,
  layers,
  valuesLayers,
  remove,
  selectedLayer,
  selectLayer,
  saveAsNew,
  width,
  positionModalShow,
  holdTimerCount,
  layersMoreThanOne,
  layersLessThanThree,
  removeLayer,
  addLayerHandler,
  getLowestVisible
}) => (
  <div style={styles.wrapper}>
    <LayersBlock
      {...{
        layers: valuesLayers,
        selectedLayer,
        selectLayer,
        layersMoreThanOne,
        layersLessThanThree,
        removeLayer,
        addLayerHandler
      }}
    />
    <div style={styles.mosaicWrapper}>
      {layers.map((layer, index) => (
        <MosaicLayoutWrapper
          {...{
            key: index,
            layer,
            index,
            width,
            positionModalShow,
            holdTimerCount,
            selectedLayer,
            getLowestVisible
          }}
        />
      ))}
    </div>
  </div>
);

export default EditBlock;
