import { observer } from 'mobx-react';
import { compose, withProps, withHandlers } from 'recompose';

const getPresetsByPage = (presets, rowsPerPage, page) =>
  presets.filter(
    (item, index) => index >= rowsPerPage * page && index < rowsPerPage * page + rowsPerPage
  );

const logic = compose(
  observer,
  withProps(({ presetsList, presetsOnPage, page: { page, pagePrev, pageNext } }) => {
    const presetTodisplay = getPresetsByPage(presetsList, presetsOnPage, page);
    const presetslistLength = presetsList.length;

    return {
      presetTodisplay,
      presetslistLength,
      page,
      pagePrev,
      pageNext,
      isPagination: presetslistLength > presetsOnPage
    };
  }),
  withHandlers({
    pagePrevHandler: ({ pagePrev }) => () => {
      pagePrev();
    },
    pageNextHandler: ({ pageNext }) => () => {
      pageNext();
    }
  })
);

export default logic;
