import { observer, inject } from 'mobx-react';
import { compose, withProps, withHandlers } from 'recompose';
import { injectIntl } from 'react-intl';
import messagesId from 'translation/messagesId';

import { roles } from 'constants/roles';

const logic = compose(
  injectIntl,
  inject('positionModal', 'editableMosaic'),
  observer,
  withProps(
    ({
      intl,
      positionModal: { target, visible, positionModalHide },
      editableMosaic: {
        getPosition: { role, changeRole, remove, changePositionNum },
        getLayersPositions,
        selectPosition,
        selectedPosition
      }
    }) => ({
      target,
      visible,
      positionModalHide,
      selectPosition,
      selectedPosition,
      role,
      changeRole,
      remove,
      changePositionNum,
      getLayersPositions,
      roles: roles.map(role => intl.formatMessage(messagesId[role]))
    })
  ),
  withHandlers({
    positionModalHideHandler: ({ positionModalHide }) => () => {
      positionModalHide();
    },
    changeRoleHandler: ({ positionModalHide, changeRole, roles }) => role => {
      positionModalHide();
      changeRole(roles.indexOf(role));
    },
    removeHandler: ({ remove, positionModalHide }) => () => {
      positionModalHide();
      remove();
    },
    changePositionNumHandler: ({
      positionModalHide,
      changePositionNum,
      selectedPosition,
      selectPosition
    }) => positionNumber => {
      positionModalHide();
      changePositionNum(selectedPosition, positionNumber);
      selectPosition(positionNumber);
    }
  })
);

export default logic;
