import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';

const AddLayerTab = ({ classes, addLayerHandler }) => (
  <div className={classes.tabRoot} onClick={addLayerHandler}>
    <IconButton className={classes.addButton} aria-label="addIcon">
      <AddIcon className={classes.addIcon} />
    </IconButton>
  </div>
);
const EnhancedAddLayerTab = withStyles(styles)(AddLayerTab);

export default EnhancedAddLayerTab;
