import React from 'react';
import { observer } from 'mobx-react';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Icon from '@material-ui/core/Icon';
import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import styles from './styles';

const ShowLayer = ({ classes, visibleOffHandler, visibleOnHandler, visible }) => (
  <Icon className={classes.visibleButton}>
    {visible ? (
      <VisibilityIcon className={classes.visibleIcon} onClick={visibleOffHandler} />
    ) : (
      <VisibilityOffIcon className={classes.visibleIcon} onClick={visibleOnHandler} />
    )}
  </Icon>
);

const EnhancedShowLayer = compose(
  withStyles(styles),
  observer
)(ShowLayer);

export default EnhancedShowLayer;
