import { compose, lifecycle, withState } from 'recompose';

import messages from 'translation/messages';
import getLocale from 'helpers/getLocale';

const defaultLocale = 'ru';

const logic = compose(
  withState('locale', 'localeChange', defaultLocale),
  withState('messagesLocale', 'messagesLocaleChange', messages[defaultLocale]),
  lifecycle({
    componentDidMount() {
      const { localeChange, messagesLocaleChange } = this.props;
      const { locale, messagesLocale } = getLocale(messages, defaultLocale);

      localeChange(locale);
      messagesLocaleChange(messagesLocale);
    }
  })
);

export default logic;
