import { observer, inject } from 'mobx-react';
import { compose, withProps, withHandlers } from 'recompose';
import { injectIntl } from 'react-intl';
import isEqual from 'lodash.isequal';

function checkLayerChanged(layers, selectedMosaic, selectedLayer, getPresetById) {
  if (selectedMosaic) {
    return isEqual(
      layers[selectedLayer],
      getPresetById(selectedMosaic.toString()).layers[selectedLayer]
    );
  }
  return layers[selectedLayer].positions.length === 0 && layers[selectedLayer].dimension === 3;
}
const logic = compose(
  injectIntl,
  inject('editableMosaic', 'mosaic'),
  observer,
  withHandlers({
    removeAllPositionsHandler: ({
      editableMosaic: {
        mosaic: { layers, selectedLayer },
        removeLayerPositions,
        openConfirmRemovePositions
      },
      mosaic: { selectedMosaic, getPresetById }
    }) => () => {
      if (checkLayerChanged(layers, selectedMosaic, selectedLayer, getPresetById)) {
        removeLayerPositions();
      } else {
        openConfirmRemovePositions();
      }
    },
    changeDimensionHandler: ({
      editableMosaic: {
        mosaic: { layers, selectedLayer }
      }
    }) => dimension => {
      const { changeDimension } = layers.get(selectedLayer);

      changeDimension(Number(dimension));
    },
    saveAsNewHandler: ({
      mosaic: { saveAsNew },
      editableMosaic: {
        mosaic: { layers }
      }
    }) => () => {
      saveAsNew(layers);
    },
    hideLowLayersHandler: ({
      editableMosaic: {
        mosaic: { hideLowLayers }
      }
    }) => () => {
      hideLowLayers();
    },
    showLowLayersHandler: ({
      editableMosaic: {
        mosaic: { showLowLayers }
      }
    }) => () => {
      showLowLayers();
    },
    changeLayerNumHandler: ({
      editableMosaic: {
        mosaic: { changeLayerNum, selectedLayer }
      }
    }) => layer => {
      if (Number(layer) !== selectedLayer) changeLayerNum(Number(layer));
    }
  }),
  observer,
  withProps(
    ({
      editableMosaic: {
        mosaic: { layers, selectedLayer, lowLayersVisibility, getLayersPositions },
        getSupportedDimensions,
        layersMoreThanOne
      }
    }) => {
      const hasLayerPositions = !!layers[selectedLayer].getPositionsLength;
      const { dimension } = layers.get(selectedLayer);
      const isLowestLayer = selectedLayer === 0;
      const isHideLowLayers = !isLowestLayer && layersMoreThanOne && lowLayersVisibility;
      const isShowLowLayers = !isLowestLayer && layersMoreThanOne && !lowLayersVisibility;
      const layersNumList = layers.map((layer, index) => index.toString());
      const supportedDimensions = getSupportedDimensions.map(item => item.toString());

      return {
        layersNumList,
        layers,
        selectedLayer: selectedLayer.toString(),
        hasLayerPositions,
        getLayersPositions,
        dimension: dimension.toString(),
        supportedDimensions,
        isHideLowLayers,
        isShowLowLayers,
        layersMoreThanOne
      };
    }
  )
);

export default logic;
