import { getParent } from 'mobx-state-tree';

const actions = self => {
  function changeRole(role) {
    getParent(self, 4).changePositionRole(self.i, role);
  }
  function remove() {
    getParent(self, 2).removePosition(self);
  }
  function selectPosition() {
    getParent(self, 5).selectPosition(self.i);
  }
  function changePositionNum(position, num) {
    getParent(self, 4).changePositionNum({ position, num });
  }
  return { changeRole, remove, selectPosition, changePositionNum };
};

export default actions;
