import React from 'react';
import LayerItem from 'components/LayerItem';
import AddLayerTab from 'components/AddLayerTab';
import Tabs from '@material-ui/core/Tabs';
import { withStyles } from '@material-ui/core/styles';
import messagesId from 'translation/messagesId';
import styles from './styles';
import logic from './logic';

export const LayersBlock = ({
  intl,
  classes,
  layers,
  selectedLayer,
  selectLayerHandler,
  layersMoreThanOne,
  layersLessThanThree,
  removeLayer,
  addLayerHandler
}) => {
  const tabsClass = {
    root: classes.tabsRoot,
    scroller: classes.scroller,
    indicator: classes.tabsIndicator
  };

  return (
    <div className={classes.wrapper}>
      <h3 style={styles.textTitle}>{intl.formatMessage(messagesId.layoutSettings)}</h3>
      <div className={classes.layersWrapper}>
        <Tabs value={selectedLayer} onChange={selectLayerHandler} classes={tabsClass}>
          {layers.map((layer, index) => (
            <LayerItem
              key={index}
              {...{ index, layersMoreThanOne, removeLayer, selectedLayer, layer }}
            />
          ))}
        </Tabs>
        {layersLessThanThree && <AddLayerTab {...{ addLayerHandler }} />}
      </div>
    </div>
  );
};
const EnhancedLayersBlock = logic(withStyles(styles)(LayersBlock));

export default EnhancedLayersBlock;
