import React from 'react';
import { compose, pure } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import styles from './styles';

const materialStyles = {
  positionPresetText: {
    flex: '1 0 auto',
    display: 'flex',
    flexDirection: 'column',
    alignSelf: 'center',
    fontSize: '40%',
    color: '#ffffff',
    margin: 0
  }
};

const rolesColors = ['#E0E0E0aa', '#80CBC4aa', '#FFB74Daa', '#BDBDBDaa', '#B3E5FCaa'];

const PresetPosition = ({ classes, positionPresetText, i, role, style, className, ...props }) => {
  const positionStyle = { ...style, ...styles.block(rolesColors[role]) };

  return (
    <div {...props} style={positionStyle} {...{ className }}>
      <Typography className={classes.positionPresetText}>{i}</Typography>
    </div>
  );
};
const EnhancedPresetPosition = compose(
  withStyles(materialStyles),
  pure
)(PresetPosition);

export default EnhancedPresetPosition;
