import React from 'react';

import { withStyles } from '@material-ui/core/styles';
import Input from '@material-ui/core/Input';
import FormLabel from '@material-ui/core/FormLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import FormHelperText from '@material-ui/core/FormHelperText';

import logic from './logic';
import styles from './styles';

export const CollCountSelect = ({ classes, theme, value, list, title, helperText, chngeHndlr }) => {
  const inputLabelClasses = { root: classes.inputLabel };
  const selectClasses = {
    icon: classes.iconSelect,
    root: classes.selectMenuRoot,
    selectMenu: classes.selectMenu
  };
  const selectInput = {
    root: classes.inputRoot
  };
  const menuItemClasses = { root: classes.menuItem };
  const formHelperClasses = { root: classes.inputLabel };

  return (
    <div className={classes.root}>
      <FormControl className={classes.formControl}>
        <FormLabel classes={inputLabelClasses} htmlFor="count-select">
          {title}
        </FormLabel>
        <Select
          value={value}
          onChange={chngeHndlr}
          input={<Input id="count-select" classes={selectInput} />}
          classes={selectClasses}
        >
          {list.map(item => (
            <MenuItem key={item} value={item} classes={menuItemClasses}>
              {item}
            </MenuItem>
          ))}
        </Select>
        <FormHelperText classes={formHelperClasses}>{helperText}</FormHelperText>
      </FormControl>
    </div>
  );
};
const EnhancedCollCountSelect = logic(withStyles(styles)(CollCountSelect));

export default EnhancedCollCountSelect;
