import React from 'react';
import messagesId from 'translation/messagesId';
import CollCountSelect from 'components/CollCountSelect';
import ActionButton from 'components/ActionButton';
import styles from './styles';

const SettingsBlock = ({
  intl,
  dimension,
  supportedDimensions,
  changeDimensionHandler,
  saveAsNewHandler,
  removeAllPositionsHandler,
  hasLayerPositions,
  getLayersPositions,
  isHideLowLayers,
  isShowLowLayers,
  hideLowLayersHandler,
  showLowLayersHandler,
  layersNumList,
  selectedLayer,
  changeLayerNumHandler,
  layersMoreThanOne
}) => (
  <div style={styles.wrapper}>
    <h3 style={styles.textTitle}>{intl.formatMessage(messagesId.controls)}</h3>
    <div style={styles.controls}>
      <div style={styles.controlsWrapper}>
        <CollCountSelect
          {...{
            title: intl.formatMessage(messagesId.grid),
            helperText: intl.formatMessage(messagesId.changeGridDimension),
            list: supportedDimensions,
            value: dimension,
            valueChngeHndlr: changeDimensionHandler
          }}
        />
        {layersMoreThanOne && (
          <CollCountSelect
            {...{
              title: intl.formatMessage(messagesId.layerNum),
              helperText: intl.formatMessage(messagesId.changeLayerNum),
              list: layersNumList,
              value: selectedLayer,
              valueChngeHndlr: changeLayerNumHandler
            }}
          />
        )}
        {hasLayerPositions && (
          <ActionButton
            {...{
              type: 'danger',
              text: intl.formatMessage(messagesId.removeAllPositions),
              onClick: removeAllPositionsHandler
            }}
          />
        )}
        {isHideLowLayers && (
          <ActionButton
            {...{
              type: 'success',
              text: intl.formatMessage(messagesId.hideLowLayers),
              onClick: hideLowLayersHandler
            }}
          />
        )}
        {isShowLowLayers && (
          <ActionButton
            {...{
              type: 'success',
              text: intl.formatMessage(messagesId.showLowLayers),
              onClick: showLowLayersHandler
            }}
          />
        )}
      </div>
      <div>
        <div style={styles.customPageControls}>
          {getLayersPositions && (
            <ActionButton
              {...{
                type: 'success',
                text: intl.formatMessage(messagesId.saveAsNew),
                onClick: saveAsNewHandler
              }}
            />
          )}
        </div>
      </div>
    </div>
  </div>
);

export default SettingsBlock;
