import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import styles from './styles';

const HoverPosition = ({ addPositionHandler, hoverX, hoverY, hoverWidth }) => (
  <div onClick={addPositionHandler} style={styles.hoverStyle(hoverX, hoverY, hoverWidth)}>
    <span style={styles.keyHover}>
      <AddIcon style={styles.iconAdd} />
    </span>
  </div>
);

export default HoverPosition;
