import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button';
import messagesId from 'translation/messagesId';
import styles from './styles';

const ConfirmChangeMozaic = ({ intl, classes, isConfirm, close, confirm }) => (
  <div>
    <Modal
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
      open={isConfirm}
      onClose={close}
    >
      <div className={classes.paper}>
        <Typography className={classes.title} variant="title" id="modal-title">
          {intl.formatMessage(messagesId.confirmRemoval)}
        </Typography>
        <Typography className={classes.text} variant="subheading">
          {intl.formatMessage(messagesId.confirmLayoutChange)}
        </Typography>
        <Typography className={classes.text} variant="subheading">
          {intl.formatMessage(messagesId.confirmLayoutDeletion)}
        </Typography>
        <div className={classes.buttonWrapper}>
          <Button className={classes.cancel} onClick={close}>
            {intl.formatMessage(messagesId.cancel)}
          </Button>
          <Button className={classes.delete} onClick={confirm}>
            {intl.formatMessage(messagesId.delete)}
          </Button>
        </div>
      </div>
    </Modal>
  </div>
);

const EnhancedConfirmChangeMozaic = withStyles(styles)(ConfirmChangeMozaic);

export default EnhancedConfirmChangeMozaic;
