import React from 'react';
import PresetPositionLayout from 'components/PresetPositionLayout';
import DeleteIcon from 'components/DeleteIcon';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import logic from './logic';

const materialStyles = {
  presetButton: {
    padding: 0,
    minWidth: 0
  }
};
const PresetMosaic = ({
  classes,
  isSelected,
  positions,
  dimension,
  isDefault,
  presetMargin,
  prstCntnrWidth,
  itemMargin,
  presetWidth,
  removeHandler,
  selectMosaicHandler,
  layers
}) => (
  <div style={styles.wrapper(presetMargin)}>
    {!isDefault && <DeleteIcon onClick={removeHandler} />}
    <Button onClick={selectMosaicHandler} className={classes.presetButton}>
      <div style={styles.resizeWrapper(presetWidth, isSelected)}>
        {layers.map(({ positions, dimension }, index) => (
          <div key={index} style={styles.layoutWrapper}>
            <PresetPositionLayout
              {...{
                positions,
                dimension,
                presetMargin,
                prstCntnrWidth,
                itemMargin,
                presetWidth
              }}
            />
          </div>
        ))}
      </div>
    </Button>
  </div>
);
const EnhancedPresetMosaic = logic(withStyles(materialStyles)(PresetMosaic));

export default EnhancedPresetMosaic;
