import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import messagesId from 'translation/messagesId';
import Popover from '@material-ui/core/Popover';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Close';
import CollCountSelect from 'components/CollCountSelect';

import styles from './styles';

const anchorOrigin = {
  vertical: 'center',
  horizontal: 'left'
};
const transformOrigin = {
  vertical: 'top',
  horizontal: 'left'
};

const PositionModal = ({
  intl,
  roles,
  getLayersPositions,
  classes,
  target,
  visible,
  positionModalHideHandler,
  selectedPosition,
  role,
  changeRoleHandler,
  removeHandler,
  changePositionNumHandler
}) => (
  <Popover
    open={visible}
    anchorEl={target}
    onClose={positionModalHideHandler}
    anchorOrigin={anchorOrigin}
    transformOrigin={transformOrigin}
  >
    <div className={classes.paper}>
      <IconButton
        className={classes.closeButton}
        aria-label="Delete"
        onClick={positionModalHideHandler}
      >
        <DeleteIcon className={classes.icon} />
      </IconButton>
      <Typography className={classes.typography}>
        {intl.formatMessage(messagesId.positionEditing, { selectedPosition })}
      </Typography>
      <div className={classes.seletcRoot}>
        <CollCountSelect
          {...{
            title: intl.formatMessage(messagesId.position),
            helperText: intl.formatMessage(messagesId.changingPositionNumber),
            list: getLayersPositions,
            value: selectedPosition,
            valueChngeHndlr: changePositionNumHandler
          }}
        />
      </div>
      <div className={classes.seletcRoot}>
        <CollCountSelect
          {...{
            title: intl.formatMessage(messagesId.role),
            helperText: intl.formatMessage(messagesId.changingPositionRole),
            list: roles,
            value: roles[role],
            valueChngeHndlr: changeRoleHandler
          }}
        />
      </div>
      <Button className={classes.buttonDelete} onClick={removeHandler}>
        {intl.formatMessage(messagesId.deletePosition)}
      </Button>
    </div>
  </Popover>
);

const EnhancedPositionModal = withStyles(styles)(PositionModal);

export default EnhancedPositionModal;
