import React from 'react';
import RGL from 'react-grid-layout';
import 'react-grid-layout/css/styles.css';
import CustomPosition from 'components/CustomPosition';
import logic from './logic';

const GridLayout = ({
  positions,
  dimension,
  itemMargin,
  rowHeight,
  width,
  onDragStop,
  onDrag,
  onDragStart,
  onResizeStart,
  onResizeStop,
  positionModalShow,
  isSelectedLayer
}) => (
  <RGL
    margin={[itemMargin, itemMargin]}
    compactType={null}
    autoSize={false}
    preventCollision
    {...{
      cols: dimension,
      maxRows: dimension,
      rowHeight,
      layout: positions,
      width,
      onDragStop,
      onDrag,
      onDragStart,
      onResizeStart,
      onResizeStop
    }}
  >
    {positions.map((position, index) => (
      <CustomPosition
        {...{ key: position.i, position, positionModalShow, rowHeight, isSelectedLayer }}
      />
    ))}
  </RGL>
);
const EnhancedGridLayout = logic(GridLayout);

export default EnhancedGridLayout;
