import { compose, withHandlers } from 'recompose';
import getMaxResizeByCollides from 'helpers/getMaxResizeByCollides';
import { observer } from 'mobx-react';
import isEqual from 'lodash.isequal';

export const logic = compose(
  withHandlers({
    onDrag: ({ holdTimerCount }) => () => {
      holdTimerCount();
    },
    onDragStart: ({ allowHoveredPositionChange }) => () => {
      allowHoveredPositionChange(false);
    },
    onDragStop: ({ changeCoordinates, allowHoveredPositionChange }) => (arr, item, itemChanged) => {
      allowHoveredPositionChange(true);
      if (!isEqual(item, itemChanged)) {
        const { i, x, y } = itemChanged;

        changeCoordinates(i, x, y);
      }
    },
    onResizeStart: ({ allowHoveredPositionChange, changeMaxWidth }) => (
      layout,
      item,
      itemChanged
    ) => {
      const { i } = itemChanged;

      changeMaxWidth({ ...getMaxResizeByCollides(layout, itemChanged), i });
      allowHoveredPositionChange(false);
    },
    onResizeStop: ({ changeWidth, allowHoveredPositionChange }) => (layout, item, itemChanged) => {
      allowHoveredPositionChange(true);
      if (!isEqual(item, itemChanged)) {
        const { i, w, h } = itemChanged;

        changeWidth(i, w, h);
      }
    }
  })
);

const composedLogic = compose(
  logic,
  observer
);

export default composedLogic;
