import { types as typesMST } from 'mobx-state-tree';
import Layer from '../Layer';

const types = {
  id: typesMST.identifier,
  selectedLayer: typesMST.optional(typesMST.number, 0),
  lowLayersVisibility: typesMST.optional(typesMST.boolean, true),
  layers: typesMST.array(Layer)
};

export default types;
