import { compose, withState, withHandlers, withProps, mapProps } from 'recompose';
import checkCollision from 'helpers/checkCollision';

import { observer } from 'mobx-react';

const logic = compose(
  withState('hoverPosition', 'hoverPositionChange', { visible: false }),
  withState('allowHoveredPosition', 'allowHoveredPositionChange', true),
  withProps(({ width, dimension }) => {
    const itemMargin = (width / 100) * 1.5;

    return {
      itemMargin,
      rowHeight: (width - itemMargin) / dimension - itemMargin
    };
  }),
  withHandlers({
    addPositionHandler: ({ hoverPositionChange, addPosition, hoverPosition: { x, y } }) => () => {
      addPosition(x, y);
      hoverPositionChange({ visible: false });
    },
    clearHoverPosition: ({ hoverPositionChange }) => () => {
      hoverPositionChange({ visible: false });
    },
    addHoverPosition: ({
      positions,
      dimension,
      itemMargin,
      rowHeight,
      width,
      hoverPositionChange,
      allowHoveredPosition,
      hoverPosition
    }) => event => {
      const { x, y, hoverX, hoverY, hoverWidth, visible } = checkCollision(
        positions,
        dimension,
        itemMargin,
        rowHeight,
        width,
        allowHoveredPosition,
        event
      );

      if (hoverPosition.hoverX !== hoverX || hoverPosition.hoverY !== hoverY) {
        hoverPositionChange({ x, y, hoverX, hoverY, hoverWidth, visible });
      }
    }
  }),
  mapProps(({ hoverPosition: { hoverX, hoverY, hoverWidth, visible }, ...props }) => ({
    hoverX,
    hoverY,
    hoverWidth,
    visible,
    ...props
  })),
  observer
);

export default logic;
