import { destroy, flow, getSnapshot } from 'mobx-state-tree';
import { baseGet, basePost, postImg } from 'helpers/fetch';
import createConfigs from 'helpers/createConfigs';
import createPng from 'helpers/createPng';
import Mosaic from '../Mosaic';

const actions = self => {
  function selectMosaic(id, isDefault) {
    self.isDefaultSelected = isDefault;
    self.selectedMosaic = Number(id);
    const list = isDefault ? self.defaultPresets : self.customPresets;
    const selectedMosaic = list.find(({ id }) => Number(id) === self.selectedMosaic);

    selectedMosaic.selectedLayer = selectedMosaic.layers.length - 1;
  }
  function openConfirmSelectMosaic(id, isDefault) {
    self.idToselectMosaic = id;
    self.isDefaultToselectMosaic = isDefault;
    self.isConfirmSelectMosaic = true;
  }
  function closeConfirmSelectMosaic() {
    self.isConfirmSelectMosaic = false;
  }
  function confirmSelectMosaic() {
    self.isConfirmSelectMosaic = false;
    selectMosaic(self.idToselectMosaic, self.isDefaultToselectMosaic);
  }
  function openConfirmRemoveMosaic(item) {
    self.idToRemoveMosaic = item.id;
    self.isConfirmRemoveMosaic = true;
  }
  function closeConfirmRemoveMosaic() {
    self.isConfirmRemoveMosaic = false;
  }
  function isLoadedOff() {
    self.isLoaded = false;
  }
  function isLoadErrorOff() {
    self.isLoadError = false;
  }
  function isSavedOff() {
    self.isSaved = false;
  }
  function isSaveErrorOff() {
    self.isSaveError = false;
  }
  function isDeleteOff() {
    self.isDelete = false;
  }
  function isDeleteErrorOff() {
    self.isDeleteError = false;
  }
  return {
    saveAsNew: flow(function* saveAsNew(layersToSave) {
      const layersSnapshot = getSnapshot(layersToSave);
      const layers = layersSnapshot.map(({ positions, dimension }) => ({ positions, dimension }));

      const { cfg, set } = createConfigs(layers);

      try {
        const id = yield basePost('/constructor/add', {
          cfg,
          set,
          mosaic: JSON.stringify({ layers })
        }).then(
          ({
            data: {
              data: {
                mosaic: { id }
              }
            }
          }) => id
        );

        yield postImg(`/constructor/img/${id}`, createPng(layers)).then(e => id);

        self.customPresets.push(
          Mosaic.create({
            id: id.toString(),
            layers
          })
        );
        self.isSaved = true;
      } catch (error) {
        self.isSaveError = true;
        console.error('Failed to save preset', error);
      }
    }),
    confirmRemoveMosaic: flow(function* confirmRemoveMosaic() {
      const item = self.customPresets.find(({ id }) => id === self.idToRemoveMosaic);
      const itemId = item.id;

      try {
        yield basePost(`/constructor/delete/${itemId}`).then(e => e);
        self.isConfirmRemoveMosaic = false;
        destroy(item);
        if (Number(itemId) === self.selectedMosaic) self.selectedMosaic = 0;
        self.isDelete = true;
      } catch (error) {
        self.isDeleteError = true;
        console.error('Failed load presets', error);
      }
    }),
    loadCustomPresets: flow(function* loadPresets() {
      try {
        self.customPresets = yield baseGet('/constructor/list').then(
          ({
            data: {
              data: { mosaics }
            }
          }) =>
            mosaics.map(({ mosaic, id }) => {
              const parsedMosaic = JSON.parse(mosaic);

              if (parsedMosaic.mozaic) {
                const { mozaic, collCount } = parsedMosaic;

                return {
                  id: id.toString(),
                  layers: [{ dimension: collCount, positions: mozaic }]
                };
              }
              const { layers } = parsedMosaic;

              return { id: id.toString(), layers };
            })
        );
        self.isLoaded = true;
      } catch (error) {
        self.isLoadError = true;
        console.error('Failed load presets', error);
      }
    }),
    selectMosaic,
    openConfirmSelectMosaic,
    closeConfirmSelectMosaic,
    confirmSelectMosaic,
    openConfirmRemoveMosaic,
    closeConfirmRemoveMosaic,
    isLoadedOff,
    isLoadErrorOff,
    isSavedOff,
    isSaveErrorOff,
    isDeleteOff,
    isDeleteErrorOff
  };
};

export default actions;
