const fullViewPresetsCout = 16;
const halfViewPresetsCout = 8;

const checkFullView = (defaultVisible, customVisible) =>
  defaultVisible && customVisible ? halfViewPresetsCout : fullViewPresetsCout;

const checkPresetsClosed = (defaultVisible, customVisible) =>
  defaultVisible === false && customVisible === false;

const actions = self => {
  function changeDefaultVisible() {
    self.defaultVisible = !self.defaultVisible;
    if (checkPresetsClosed(self.defaultVisible, self.customVisible)) {
      self.customVisible = true;
    }
    self.presetsOnPage = checkFullView(self.defaultVisible, self.customVisible);
  }
  function changeCustomVisible() {
    self.customVisible = !self.customVisible;
    if (checkPresetsClosed(self.defaultVisible, self.customVisible)) {
      self.defaultVisible = true;
    }
    self.presetsOnPage = checkFullView(self.defaultVisible, self.customVisible);
  }
  return { changeDefaultVisible, changeCustomVisible };
};

export default actions;
