import getCookie from './getCookie';

function getLocale(messages, defaultLocale) {
  let locale = getCookie('lang');
  let messagesLocale = messages[locale];

  if (!messagesLocale) {
    locale = navigator.languages
      ? navigator.languages[0]
      : navigator.language || navigator.userLanguage || defaultLocale;
    locale = locale.substring(0, 2);
    messagesLocale = messages[locale];
  }

  if (!messagesLocale) {
    locale = defaultLocale;
    messagesLocale = messages[locale];
  }
  return { locale, messagesLocale };
}
export default getLocale;
