const actions = self => {
  function closeActionMessage() {
    self.isActionMessage = false;
  }
  function setActionMessage(isActionMessage, actionMessageState, actionMessageText) {
    self.isActionMessage = isActionMessage;
    self.actionMessageState = actionMessageState;
    self.actionMessageText = actionMessageText;
  }
  return { closeActionMessage, setActionMessage };
};

export default actions;
