import { observer } from 'mobx-react';
import { compose, withProps, withHandlers } from 'recompose';
import { injectIntl } from 'react-intl';

export const logic = compose(
  withProps(({ layer: { visible }, index, selectedLayer }) => {
    const lowestThanSelected = index < selectedLayer;

    return { lowestThanSelected, visible };
  }),
  withHandlers({
    removeLayerHandler: ({ removeLayer, index }) => () => {
      removeLayer(index);
    },
    visibleOffHandler: ({ layer: { visibleOff } }) => () => {
      visibleOff();
    },
    visibleOnHandler: ({ layer: { visibleOn } }) => () => {
      visibleOn();
    }
  })
);
const composedLogic = compose(
  injectIntl,
  observer,
  logic
);

export default composedLogic;
