const actions = self => {
  function positionModalShow(target) {
    if (self.holdTimer < 5) {
      self.target = target;
      self.visible = true;
    } else {
      self.holdTimer = 0;
    }
  }
  function positionModalHide() {
    self.visible = false;
  }
  function holdTimerCount() {
    self.holdTimer++;
  }
  return { positionModalShow, positionModalHide, holdTimerCount };
};

export default actions;
