import { compose, withHandlers, mapProps, onlyUpdateForKeys } from 'recompose';

const logic = compose(
  withHandlers({
    chngeHndlr: ({ valueChngeHndlr }) => event => {
      valueChngeHndlr(event.target.value);
    }
  }),
  mapProps(({ value, list, title, helperText, chngeHndlr }) => ({
    value,
    list,
    title,
    helperText,
    chngeHndlr
  })),
  onlyUpdateForKeys(['value', 'list'])
);

export default logic;
